import ClientsSlider from './ClientsSlider';
import { getSheetData } from '../lib/fetchGoogleSheet';

export default async function Clients() {
  const rows = await getSheetData('Home');
  const clientLogo = rows.find((r: any) => r.Section === 'clientLogos');
  const clients = clientLogo ? Object.values(clientLogo).filter((v: any) => v && v.includes('.PNG')) : [];

  return <ClientsSlider clients={clients} />;
}
